import { useState } from 'react';
import { Container, Form, Button, Row, Col } from 'react-bootstrap';
import axios from 'axios';

const AddHomework = () => 
{
    const [homework, setHomework] = useState({})

    const handleHWChange = (event) => 
    {
		const name = event.target.name;
		const value = event.target.value;
		setHomework((values) => ({ ...values, [name]: value }));
		const token = localStorage.getItem("accessToken");
		setHomework((values) => ({ ...values, accessToken: token }));
	}

    const handleHWSubmit = (event) => 
    {
		event.preventDefault();
		axios.post(`${process.env.REACT_APP_API_LINK}/homework/add`, homework)
			.then((response) => {
				alert(response.data.message);
			});
	}

    return (
        <>
            <Container className='mt-5 p-5 m-auto shadow-sm rounded header' style={{maxWidth: '60%'}}>
                <h1 className='text-center text-light'></h1>
                <Form onSubmit={handleHWSubmit}>
                    <Row>
                        <Col>
                            <Form.Label className='text-light'></Form.Label>
                            <Form.Select aria-label='Default select example' name='subject' onChange={handleHWChange}>
                                <option selected disable hidden></option>
                                <option value=' 2'> 2</option>
                                <option value=''></option>
                                <option value=' 1'> 1</option>
                                <option value=' 3'> 3</option>
                                <option value=' 3'> 3</option>
                                <option value=' 2'> 2</option>
                                <option value=' 3'> 3</option>
                                <option value=' 3'></option>
                                <option value=' 3'> 3</option>
                                <option value=' 3'> 3</option>
                                <option value=' 3'> 3</option>
                                <option value=''></option>
                                <option value=''></option>
                                <option value=''></option>
                                <option value=''></option>
                                <option value=' 3'> 3</option>
                                <option value=''></option>
                                <option value=''></option>
                            </Form.Select>
                        </Col>
                    </Row><br/>
                    <Row>
                        <Col>
                            <Form.Group controlId='formBasicPassword'>
                                <Form.Label className='text-light'></Form.Label>
                                <Form.Control type='text' placeholder='' name='topic' onChange={handleHWChange}/>
                            </Form.Group>
                        </Col>
                        <Col>
                            <Form.Group className='mb-3' controlId='formDateDue'>
                            <Form.Label className='text-light'></Form.Label>
                            <Form.Control type='datetime-local' name='dateDue' onChange={handleHWChange}/>
                            </Form.Group>
                        </Col>
                    </Row>
                    <Form.Group className='mb-3' controlId='formDescription'>
                        <Form.Label className='text-light'></Form.Label>
                        <Form.Control as='textarea' rows={3} name='description' placeholder='' onChange={handleHWChange}/>
                    </Form.Group>
                    <Button variant='primary btn-block' type='submit'></Button>
                </Form>
            </Container><br/>
            
        </>
    );
}

export default AddHomework;